import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { IOptionsPinConfirmExposed } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-password-confirm/ps-confirm-pin/ps-confirm-pin.component.interfaces';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { CardManagementVO, IChangePinVO } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-interfaces';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { IOptionsPsContainerLookupOptionComponentExposed, IOptionsPsContainerPanel, IOptionsPsLabel, IOptionsTemplateStepper } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { OmniBasePage } from './../omni-base/omni-base.page';

@Component({
  selector: 'app-reset-pin-request',
  templateUrl: './reset-pin-request.page.html',
  styleUrls: ['./reset-pin-request.page.scss'],
})
export class ResetPinRequestPage extends OmniBasePage implements OnInit {

  CardManagementVO: CardManagementVO = {};
  change = true;
  pageCustomizationMap: Map<string, any>;

  itemCard: any = {};
  cardOptions: IOptionsPsContainerLookupOptionComponentExposed = {};

  constructor(public commonProv: PsCommonService, private navService: PsNavigatorService) {
    super();
  }
  private formGroup = new FormGroup({});
  changePinVO: IChangePinVO = {};
  public stepperOptions: IOptionsTemplateStepper = {
    stepperName: 'card_pin_reset',
    isHorizontalStepper: true,
    group: this.formGroup,
    numberOfSteps: 1,
    namesofSteps: ['card_pin_reset_step1'],
    submitOptions: {
      submitServiceUrl: PsCommonSettings.serviceUrl.commonSubmitAction,
      group: this.formGroup,
      preCallFunction: {
        func() {
          return new Promise<any>((resolve, reject) => {
            resolve(this.executionClass.prepareSubmitRequest());
          });
        },
        executionClass: this,
        params: [this]
      },
    },
    requestObject: this.CardManagementVO
  };
  resetPinHintOptions: IOptionsPsLabel = {
    labelKey: 'reset_pin_hint_key',
    fcName: 'resetPinHint'
  };
  panelOptions1: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'card_information_key',
    iconName: 'document',
    expanded: true
  };


  pinConfirmOptions: IOptionsPinConfirmExposed = {
    group: this.formGroup,
    password: {
      labelKey: 'new_pin_key',
      placeHolder: 'new_pin_key',
      fcName: 'newPin',
      group: this.formGroup,
    },
    confirmPassword: {
      labelKey: 'confirm_new_pin_key',
      placeHolder: 'confirm_new_pin_key',
      fcName: 'confNewPin',
      group: this.formGroup,
    },
    showPasswordHint: false,
    requestObject: this.CardManagementVO
  };


  ngOnInit() {
    super.init();
    this.baseFormGroup = this.formGroup;
    this.CardManagementVO = this.navService.getAllParams() ? this.navService.getAllParams().ScreenVO ? this.navService.getAllParams().ScreenVO : this.navService.getAllParams() : {};
    // this.commonProv.copyObject(this.options.requestObject, this.CardManagementVO, true, false);
    // delete this.stepperOptions.requestObject.key;
    if (this.CardManagementVO.action === CommonBussinessConstant.CARD_ACTION_TYPE_PIN_CHANGE) {
      this.change = true;
    } else if (this.CardManagementVO.action === CommonBussinessConstant.CARD_ACTION_TYPE_PIN_RESET) {
      this.change = false;
    }

    // Added by: M.Fawzy for BUG #1345782 - Apply mandatory customization on newPin, confNewPin based on visibility 
    this.pageCustomizationMap = this.commonProv.returnOperCustomization();
    if (this.pageCustomizationMap.get(this.pinConfirmOptions.password.fcName) != undefined &&
      this.pageCustomizationMap.get(this.pinConfirmOptions.password.fcName).IS_VISIBLE === 1) {
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.pinConfirmOptions.password.fcName], 1);
    }
    if (this.pageCustomizationMap.get(this.pinConfirmOptions.confirmPassword.fcName) != undefined &&
      this.pageCustomizationMap.get(this.pinConfirmOptions.confirmPassword.fcName).IS_VISIBLE === 1) {
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.pinConfirmOptions.confirmPassword.fcName], 1);
    }
    // End M.Fawzy

    this.itemCard = this.CardManagementVO;
    this.cardOptions.formGroup = this.formGroup;
    this.cardOptions.labelsValueMap = this.navigationServices.getParamKey('labelsValueMap');
    this.cardOptions.headerMap = this.navigationServices.getParamKey('headerMap');
    this.cardOptions.showInitialCardValues = this.navigationServices.getParamKey('showInitialCardValues');
  }
  
  prepareSubmitRequest() {
    // Added by: M.Fawzy for BUG #1350481 - Removing the key property to avoid the security error
    delete this.CardManagementVO['key'];
    delete this.formGroup.controls.formData.value['key'];
  }
}
