import { Component, HostBinding, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { ViewWillEnter } from '@ionic/angular';
import { IOptionsPsMenuExposed } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-menu/ps-complex-menu.component.interfaces';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IOptionsPsTemplateView } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { LoggerService } from '../../commonSRC/psServices/logger/logger.service';
import { OmniBasePage } from '../omni-base/omni-base.page';


@Component({
  selector: 'sub-menu',
  templateUrl: './sub-menu.page.html'
})
export class SubMenuPage extends OmniBasePage implements OnInit, ViewWillEnter {
  formGroup: FormGroup = new FormGroup({});
  public templateViewOptions: IOptionsPsTemplateView = {
    group: this.formGroup
  };
  menuOptions: IOptionsPsMenuExposed = {};
  navParam: any;
  parentMenuOperId: null;

  @HostBinding('id') subMenuId = 'sub_menu_' + PsCommonSettings.oper_ID;
  constructor(public loggerP: LoggerService, public navService?: PsNavigatorService) {
    super();
  }

  ngOnInit() {
    super.init();
  }

  ionViewWillEnter() {
    this.navParam = this.navService.getAllParams();
    if (this.navParam.navigationType != 'backward' || CommonUtils.isMobile()) {
      this.menuOptions.parentMenuOperId = 0;
      if (this.navParam?.parentMenuOperId) {
        setTimeout(() => {
          this.menuOptions.parentMenuOperId = this.navParam?.parentMenuOperId;
        }, 100);
      }
    }

  }

}
