import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { PsAccountsListComponent } from 'src/app/commonBussinessSRC/psComponents/ps-accounts-list/ps-accounts-list.component';
import { IPsComplexPayIdTypesDetailsOptionsExposed } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-payId-types-details/ps-complex-payId-types-details.component.interfaces';
import { IOptionsPsLookupOwnAccountsExposed } from 'src/app/commonBussinessSRC/psComponents/ps-lookup/ps-lookup-own-accounts/ps-lookup-own-accounts.component.interfaces';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { IOptionsPsContainerPanel, IOptionsTemplateStepper } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { IOptionsPsInputVarcharExposed } from './../../commonBussinessSRC/psComponents/ps-keyin-input/ps-input-varchar/ps-input-varchar.component.interfaces';
import { CommonBussinessConstant } from './../../commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { OmniBasePage } from './../omni-base/omni-base.page';

@Component({
  selector: 'add-payId',
  templateUrl: './add-payId.page.html',
  styleUrls: ['./add-payId.page.scss'],
})
export class AddPayIdPage extends OmniBasePage implements OnInit {

  public group = new FormGroup({});
  payIdVO: any = {};
  stepperOptions: IOptionsTemplateStepper = {
    stepperName: 'create_payid_stepper',
    isHorizontalStepper: true,
    numberOfSteps: 1,
    namesofSteps: ['payid_details'],
    group: this.group,
    submitOptions: {
      submitServiceUrl: PsCommonSettings.serviceUrl.commonSubmitAction,
      group: this.group
    },
    requestObject: this.payIdVO
  }

  public panelOptionsBenf: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'payid_details_key',
    iconName: 'local-benef-details',
    expanded: true
  };

  nickNameOptions: IOptionsPsInputVarcharExposed = {
    group: this.group,
    labelKey: 'nickname_key',
    placeHolder: 'enter_payid_nickname_key',
    fcName: 'nickName'
  }
  complexPayIdTypeDetails: IPsComplexPayIdTypesDetailsOptionsExposed = {
    payIdTypesOptions: {
      fcName: 'payIdType',
      group: this.group,
    },
    mobileOptions: {
      fcName: 'mobileNumber',
      labelKey: 'mobile_no_key',
      placeHolder: 'enter_mobile_no_key',
      group: this.group
    },
    emailOptions: {
      fcName: 'emailAddress',
      labelKey: 'email_address_key',
      placeHolder: 'enter_email_address_key',
      group: this.group
    },
    phoneOptions: {
      fcName: 'phoneNumber',
      labelKey: 'phone_number_key',
      placeHolder: 'enter_phone_number_key',
      group: this.group
    }
  }

  public linkedAccountListOptions: IOptionsPsLookupOwnAccountsExposed = {
    labelKey: 'linked_account_key',
    placeHolder: 'select_liked_account_number_key',
    component: PsAccountsListComponent,
    group: this.group,
    accountAllowedCurrencies: [],
    accountAllowedTypes: [],
    glTypes: 'G',
    fcName: 'linkedAccount',
    accountType: 'G',
    fromTo: 'from',
    requestObject: this.payIdVO
  };

  purposeOptions: IOptionsPsInputVarcharExposed = {
    group: this.group,
    labelKey: 'purpose_key',
    placeHolder: 'enter_purpose_key',
    fcName: 'purpose'
  }
  isPayIdBenef = false;
  userInformation;

  constructor(public navService: PsNavigatorService) {
    super();
  }

  ngOnInit() {
    super.init();
    this.baseFormGroup = this.group;
    this.isPayIdBenef = PsCommonSettings.oper_ID == CommonBussinessConstant.PAYID_BENEFICIARY_OPER_ID;
    this.commonProv.copyObject(this.payIdVO, this.navService.getAllParams() ? this.navService.getAllParams().ScreenVO ? this.navService.getAllParams().ScreenVO : this.navService.getAllParams() : {});
  }

  onAccountChange(event) {
    this.commonProv.setValInsideNestedObj('currency', event.currency, this.payIdVO)
  }
  applyCustomization(event) {
    if (!this.isPayIdBenef) {
      this.userInformation = this.commonProv.session.getValueOf(ConstantCommon.USERINFO);
      if (event.itemValue == "M") {
        this.commonProv.setValInsideNestedObj(this.complexPayIdTypeDetails.mobileOptions.fcName, this.userInformation.customerInfoCO.userMobileNumber, this.payIdVO)
        setTimeout(() => {
          this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.complexPayIdTypeDetails.mobileOptions.fcName], 1)
        }, 500);
      } else if (event.itemValue == "E") {
        this.commonProv.setValInsideNestedObj(this.complexPayIdTypeDetails.emailOptions.fcName, this.userInformation.email, this.payIdVO)
        setTimeout(() => {
          this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.complexPayIdTypeDetails.emailOptions.fcName], 1)
        }, 500);
      } else if (event.itemValue == "P") {
        this.commonProv.setValInsideNestedObj(this.complexPayIdTypeDetails.phoneOptions.fcName, this.userInformation.customerInfoCO.homeTel, this.payIdVO)
        setTimeout(() => {
          this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.complexPayIdTypeDetails.phoneOptions.fcName], 1)
        }, 500);
      }
    }
  }
}  
