import { AfterViewInit, Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { PsAccountsListComponent } from 'src/app/commonBussinessSRC/psComponents/ps-accounts-list/ps-accounts-list.component';
import { IOptionsPsComplexAmountExposed } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-amount/ps-complex-amount.component.interfaces';
import { PsComplexDealDetailsComponent } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-deal-details/ps-complex-deal-details.component';
import { PsComplexExchangeComponent } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-exchange/ps-complex-exchange.component';
import { IOptionsPsComplexExchnageExposed } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-exchange/ps-complex-exchange.component.interfaces';
import { IOptionsPsLookupOwnAccountsExposed } from 'src/app/commonBussinessSRC/psComponents/ps-lookup/ps-lookup-own-accounts/ps-lookup-own-accounts.component.interfaces';
import { IOptionsPsLookupOwnDealsExposed } from 'src/app/commonBussinessSRC/psComponents/ps-lookup/ps-lookup-own-deals/ps-lookup-own-deals.component.interfaces';
import { PsCommonBusinessSettings } from 'src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { EventEmitterService } from 'src/app/commonSRC/psServices/event-emitter/event-emitter.service';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { IOptionsPsContainerPanel, IOptionsTemplateStepper } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { OmniBasePage } from 'src/app/pages/omni-base/omni-base.page';
import { PsCommonSettings } from '../../commonSRC/psServices/models/ps-common.settings';
import { CommonBussinessConstant } from './../../commonBussinessSRC/psServices/models/ps-common-bussiness-constant';

@Component({
  selector: 'app-early-settlement',
  templateUrl: './early-settlement.page.html',
  styleUrls: ['./early-settlement.page.scss'],
})
export class EarlySettlementPage extends OmniBasePage implements OnInit, AfterViewInit {
  formGroup: FormGroup = new FormGroup({});
  earlySettlementVO = {};
  stepperOptions: IOptionsTemplateStepper = {
    isHorizontalStepper: true,
    numberOfSteps: 1,
    namesofSteps: ['early_settlement_step1'],
    group: this.formGroup,
    requestObject: this.earlySettlementVO,
    submitOptions: {
      extraParams: {},
      submitServiceUrl: PsCommonBusinessSettings.serviceUrl.accountOpeningEndPoint, // PsCommonBusinessSettings.serviceUrl.updateMaturityInstructions,
      group: this.formGroup,
      postCallFunction: {
        func(response) {
          return new Promise<any>((resolve, reject) => {
            resolve(this.executionClass.serviceRes(response));
          });
        },
        params: [this],
        executionClass: this
      },
    }
  };
  panelOptionsStep1: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'early_settlement_key',
    iconName: 'document',
    expanded: true
  };
  psComplexAmountOptions: IOptionsPsComplexAmountExposed = {
    currenciesOptions: {
      labelKey: 'currency_key',
      placeHolder: 'currency_key',
      group: this.formGroup,
      fcName: 'currency'
    },
    amountOptions: {
      labelKey: 'outstanding_amount_key',
      placeHolder: 'enter_outstanding_amount_key',
      fcName: 'inputAmount',
      group: this.formGroup,
      type: 'amount',
      decimalPoints: 3
    }
  };
  public accountListOptions: IOptionsPsLookupOwnAccountsExposed = {
    labelKey: 'from_account_key',
    placeHolder: 'select_from_account_key',
    component: PsAccountsListComponent,
    group: this.formGroup,
    accountAllowedCurrencies: [],
    accountAllowedTypes: [],
    glTypes: 'G',
    fcName: 'fromAccount',
    accountType: 'G',
    fromTo: 'from',
    requestObject: this.earlySettlementVO
  };
  public ownDealsListOptions: IOptionsPsLookupOwnDealsExposed = {
    labelKey: 'financing_deal_key',
    placeHolder: 'select_financing_deal_key',
    fcName: 'financingDeal',
    group: this.formGroup,
    component: PsComplexDealDetailsComponent,
    requestObject: this.earlySettlementVO
  }

  public complexExchangeOptions: IOptionsPsComplexExchnageExposed = {
    editableMode: false,
    showToCurrencyOptions: true,
    fromAmountOptions: {
      currency: '',
      currenciesOptions: {
        placeHolder: 'currency_key',
        labelKey: 'currency_key',
        fcName: 'fromCurrency',
        group: this.formGroup
      },
      amountOptions: {
        labelKey: 'amount_key',
        placeHolder: 'enter_amount_key',
        fcName: 'transactionAmount',
        group: this.formGroup,
        decimalPoints: 2
      }
    },
    toAmountOptions: {
      currency: '',
      currenciesOptions: {
        placeHolder: 'exchange_currency_key',
        labelKey: 'exchange_currency_key',
        fcName: 'toCurrency',
        group: this.formGroup
      },
      amountOptions: {
        labelKey: 'exchange_amount_key',
        placeHolder: 'enter_amount_key',
        fcName: 'toAmount',
        group: this.formGroup,
        decimalPoints: 2
      }
    },
    requestObject: this.earlySettlementVO,
    group: this.formGroup
  };
  @ViewChild(PsComplexExchangeComponent) exchangeChild: PsComplexExchangeComponent;
  constructor(public commonProv: PsCommonService, private navService: PsNavigatorService, public loggerP: LoggerService, public eventEmitterService: EventEmitterService, private omniPull?: OmniPullService) {
    super();
  }

  ngOnInit() {
    PsCommonSettings.oper_ID = CommonBussinessConstant.EARLY_SETTTLEMENT_OPER_ID;
  }

  ngAfterViewInit() {
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.complexExchangeOptions.fromAmountOptions.currenciesOptions.fcName], 1);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.complexExchangeOptions.toAmountOptions.currenciesOptions.fcName], 1);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.complexExchangeOptions.toAmountOptions.amountOptions.fcName], 1);
  }

  public onDealChange(event) {
    if (event) {
      this.complexExchangeOptions.fromAmountOptions.currency = event.currency;
      this.complexExchangeOptions.fromAmountOptions.currencyCode = event.currencyCode;
      this.complexExchangeOptions.fromAmountOptions.amount = Number(event.outstandingBalance);

      setTimeout(() => {
        this.stepperOptions.requestObject[this.complexExchangeOptions.fromAmountOptions.currenciesOptions.fcName] = event.currencyCode;
        this.stepperOptions.requestObject[this.complexExchangeOptions.fromAmountOptions.amountOptions.fcName] = Number(event.outstandingBalance);
      }, 1);
    }
  }

  public onAccountChange(toAccount) {
    if (toAccount) {
      this.complexExchangeOptions.toAmountOptions.currency = toAccount.currencyBriefNameEnglish;
      this.complexExchangeOptions.toAmountOptions.currencyCode = toAccount.currency;

      setTimeout(() => {
        this.stepperOptions.requestObject[this.complexExchangeOptions.toAmountOptions.currenciesOptions.fcName] = toAccount.currency;
      }, 1);
    }
  }

}
