import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { PsAccountsListComponent } from 'src/app/commonBussinessSRC/psComponents/ps-accounts-list/ps-accounts-list.component';
import { IOptionsPsLookupOwnAccountsExposed } from 'src/app/commonBussinessSRC/psComponents/ps-lookup/ps-lookup-own-accounts/ps-lookup-own-accounts.component.interfaces';
import { IOptionsPsLovFileTypesExposed } from 'src/app/commonBussinessSRC/psComponents/ps-select-dropdown/ps-dropdown-lov/ps-lov-file-types/ps-lov-file-types.component.interface';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { PsCommonBusinessSettings } from 'src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { IOptionsPsActionIcon, IOptionsPsContainerPanel, IOptionsPsFileUploadComponent, IOptionsPsKeyinInput,IOptionsPsLabelHeader, IOptionsPsTemplateForm, IOptionsTemplateStepper } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { OmniBasePage } from 'src/app/pages/omni-base/omni-base.page';
import { ConstantCommon } from './../../commonSRC/psServices/models/common-constant';
import { PsApplicationSettings } from 'src/app/commonSRC/psServices/models/ps-app-settings';


@Component({
  selector: 'app-bulk-payment',
  templateUrl: './bulk-payment.page.html',
  styleUrls: ['./bulk-payment.page.scss'],
})
export class BulkPaymentPage extends OmniBasePage implements OnInit {
  bulkPaymentForm: FormGroup = new FormGroup({});
  defaultVO: any = {};
  public options: IOptionsPsTemplateForm = {
    group: this.bulkPaymentForm
  };
  stepperOptions: IOptionsTemplateStepper = {
    isHorizontalStepper: true,
    numberOfSteps: 1,
    namesofSteps: ['bulk_payment_step1'],
    group: this.bulkPaymentForm,
    submitOptions: {
      extraParams: {},
      submitServiceUrl: PsCommonBusinessSettings.serviceUrl.bulkPaymentEndPoint,
      group: this.bulkPaymentForm,
      postCallFunction: {
        func(response) {
          return new Promise<any>((resolve, reject) => {
            resolve(this.executionClass.serviceRes(response));
          });
        },
        params: [this],
        executionClass: this
      },
    },
    requestObject: this.defaultVO
  };
  downloadIconOptions: IOptionsPsActionIcon = {
    iconName: 'download',
    psClass: 'download-attachment'
  };
  headerOptions: IOptionsPsLabelHeader = {
    labelKey: 'bulk_salary_templates_key'
  }
  panelOptionsStep1: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'bulk_salary_upload_key',
    iconName: 'document',
    expanded: true
  };
  fileUploadOptions: IOptionsPsFileUploadComponent = {
    multiple: false,
    fcName: 'selectedFileData',
    group: this.bulkPaymentForm,
    hintLabel: 'add_a_hint_key',
    labelKey: 'upload_files_key',
    revertToOldValue: false
  };
  public accountListOptions: IOptionsPsLookupOwnAccountsExposed = {
    labelKey: 'from_account_key',
    placeHolder: 'select_from_account_key',
    component: PsAccountsListComponent,
    currency: 'USD',
    group: this.bulkPaymentForm,
    accountAllowedCurrencies: [],
    accountAllowedTypes: [],
    glTypes: 'G',
    fcName: 'fromAccount',
    accountType: 'G',
    fromTo: 'from',
    requestObject: this.defaultVO,
  };
  fileTypesOptions: IOptionsPsLovFileTypesExposed = {
    group: this.bulkPaymentForm,
    fcName: 'fileTypeOptions'
  };
  // By Mohanad - AZDB-5695 - 01092024
  batchNoOptions: IOptionsPsKeyinInput = {
    fcName: 'batchNo',
    group: this.bulkPaymentForm,
    placeHolder: 'enter_batch_no_key',
    labelKey: 'batch_no_key'
  };
  offlineScreen: boolean;

  rowCountOptions: IOptionsPsKeyinInput = {
    fcName: 'rowCount',
    group: this.bulkPaymentForm,
    placeHolder: 'enter_row_count_key',
    labelKey: 'number_of_lines_key'
  };
  amountSumOptions: IOptionsPsKeyinInput = {
    fcName: 'amountSum',
    group: this.bulkPaymentForm,
    placeHolder: 'enter_amount_sum_key',
    labelKey: 'total_amount_key'
  };
  listOfTemplatesFiles = []
  constructor(public commonProv: PsCommonService, public loggerP: LoggerService, private omniPull: OmniPullService, private navService: PsNavigatorService) {
    super();
  }

  ngOnInit() {
    super.init();
    this.baseFormGroup = this.bulkPaymentForm;
    this.offlineScreen = (PsCommonSettings.oper_ID == CommonBussinessConstant.OFFLINE_BULK_PAYMENT_OPER_ID);
  }

  ionViewDidEnter() {
    super.ionViewDidEnter();
    this.loadBulkPaymentTemplates();
    // Add batch number field to hold the value returning from PWS file validation service to save it into oc_data_save so checker can call the report with batchNo
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.batchNoOptions.fcName], 0);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.accountListOptions.fcName], 0);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.rowCountOptions.fcName, this.amountSumOptions.fcName, this.batchNoOptions.fcName], 1);
  }

  loadBulkPaymentTemplates() {
    let bulkPaymentTemplates: any = PsApplicationSettings.CLIENT_ASSETS_CONFIG.BULK_PAYMENT_TEMPLATES ? PsApplicationSettings.CLIENT_ASSETS_CONFIG.BULK_PAYMENT_TEMPLATES : [];
    bulkPaymentTemplates.forEach(templateURL => {
      this.omniPull.omniCommon.common.http.http.get(templateURL, { responseType: 'blob' }).subscribe(file => {
        const reader = new FileReader();
        reader.onloadend = () => {
          var templateName = templateURL.substr(templateURL.lastIndexOf('/') + 1);
          let fileUrlB64 = reader.result.toString().split(',')[1];
          this.listOfTemplatesFiles.push({ fileUrlB64: fileUrlB64, fileName: templateName, fileSize: file.size });
        }
        reader.readAsDataURL(file);
      });
    });

  }

  downloadFile(fileIndex: number) {
    this.commonProv.downloadFile(this.listOfTemplatesFiles[fileIndex].fileUrlB64, this.listOfTemplatesFiles[fileIndex].fileName, this.listOfTemplatesFiles[fileIndex].fileName.split('.')[1]);
  }

}
