import { Component, Input, OnInit } from '@angular/core';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { IOptionsPsDropdownLov } from '../ps-dropdown-lov.component.interfaces';
import { LoggerService } from './../../../../../commonSRC/psServices/logger/logger.service';
import { PsDropdownLovComponent } from './../ps-dropdown-lov.component';
import { IPayIdTypesLovOptionsExposed } from './ps-lov-payId-types.component.interfaces';

@Component({
  selector: 'ps-lov-payId-types',
  templateUrl: './ps-lov-payId-types.component.html',
  styleUrls: ['./ps-lov-payId-types.component.scss'],
})
export class PsLovPayIdTypesComponent extends PsDropdownLovComponent implements OnInit {
  @Input() options: IPayIdTypesLovOptionsExposed;
  public defaultOptions: IOptionsPsDropdownLov = {
    labelKey: 'payid_type_key',
    placeHolder: 'select_payid_type_key',
    lovTypeId: CommonBussinessConstant.LOV_TYPE_ID_BENEF_PAYID_TYPES
  };

  constructor(public common: PsCommonService, public logger: LoggerService) {
    super(common, logger);
  }

  ngOnInit() {
    this.commonProv.copyObject(this.defaultOptions, this.options, false);
    //demo
    this.defaultOptions.listOfOptions = [
      {
        itemValue: 'M',
        description: 'Mobile Number',
        selectedObj: {
          itemValue: 'M',
          description: 'Mobile Number',
        }
      },
      {
        itemValue: 'E',
        description: 'Email Address',
        selectedObj: {
          itemValue: 'E',
          description: 'Email Address',
        }
      },
      {
        itemValue: 'P',
        description: 'Phone Number',
        selectedObj: {
          itemValue: 'P',
          description: 'Phone Number',
        }
      }
    ]
  }

  onBenefTypeSelected(event) {
    this.onPsChange.emit(event);
  }

}
