import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { PsCommonBusinessSettings } from 'src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings';
import { IOptionsPsContainerPanel, IOptionsPsLabel, IOptionsPsSelectToggle, IOptionsPsTemplateForm } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { OmniBasePage } from 'src/app/pages/omni-base/omni-base.page';

@Component({
  selector: 'app-delink-payId',
  templateUrl: './delink-payId.page.html',
  styleUrls: ['./delink-payId.page.scss'],
})
export class DelinkPayIdPage extends OmniBasePage implements OnInit {
  private formGroup = new FormGroup({});

  public options: IOptionsPsTemplateForm = {
    group: this.formGroup,
    submitOptions: {
      extraParams: {
      },
      group: this.formGroup,
      submitServiceUrl: PsCommonBusinessSettings.serviceUrl.delinkAccount,
    }
  };
  panelOptions1Step1: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'delink_key',
    iconName: 'delink',
    expanded: true,

  };
  public totallyToggleOptions: IOptionsPsSelectToggle = {
    labelKey: 'delink_totally_key',
    group: this.formGroup,
    fcName: 'delinkTotally',
    psClass: 'toggleColor',
  };
  public toOtherTransferOptions: IOptionsPsSelectToggle = {
    labelKey: 'transfer_to_other_bank_key',
    group: this.formGroup,
    fcName: 'toOther',
    psClass: 'toggleColor',
  };
  delinkPnelHintOptions: IOptionsPsLabel = {
    labelKey: 'delink_hint_key',
    position: "fixed"
  };

  ngOnInit() {
    super.init()
  }
  onToggleClicked(event, field) {
    if (field == "totally" && event.newValue) {
      this.formGroup.controls[this.toOtherTransferOptions.fcName].setValue(false)
    } else if (field == "toOther" && event.newValue) {
      this.formGroup.controls[this.totallyToggleOptions.fcName].setValue(false)
    }
  }



}
