import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { IOmniTrustedDevices, IOmniTrustedDevicesResponse, IOptionsPsOptionTrustedDevicesExposed } from 'src/app/commonBussinessSRC/psComponents/ps-trusted-devices-list/ps-trusted-devices-list.component.interface';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { IOptionsPsTemplateView } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { PsCommonService } from '../../commonSRC/psServices/ps-common/ps-common.service';
import { OmniBasePage } from '../omni-base/omni-base.page';


@Component({
  selector: 'app-trusted-devices-list',
  templateUrl: './trusted-devices-list.page.html',
})
export class TrustedDevicesListPage extends OmniBasePage implements OnInit {
  private formGroup = new FormGroup({});
  public templateViewOptions: IOptionsPsTemplateView = {
    group: this.formGroup
  };
  requestWasSent = true;
  trustedDevicesListOptions: IOptionsPsOptionTrustedDevicesExposed = {
    group: this.formGroup,
    isEditable: true
  };
  constructor(public logger: LoggerService, public commonProv: PsCommonService, public loggerP: LoggerService, private omniPull: OmniPullService, private navService: PsNavigatorService) {
    super();
  }

  ngOnInit() {
    super.init();
  }
  ionViewWillEnter() {
    super.viewWillEnter();
    this.returnTrustedDevicesList({});
  }
  private async returnTrustedDevicesList(requestData: any) {
    this.requestWasSent = true;
    const result = await this.omniPull.returnTrustedDevicesList(requestData).catch(error => {
      this.logger.error('Error: While fetching user trusted devices  :', error);
    });

    if (result && result.gridModel.length > 0) {
      this.trustedDevicesListOptions.listOfOptions = this.populateTrustedDevices(result.gridModel);
    } else {
      this.trustedDevicesListOptions.listOfOptions = [];
    }
    this.requestWasSent = false;
  }

  reloadList() {
    this.returnTrustedDevicesList({});
  }

  private populateTrustedDevices(trustedDevices: IOmniTrustedDevicesResponse[]): IOmniTrustedDevices[] {
    const list: IOmniTrustedDevices[] = [];
    for (const iterator of trustedDevices) {
      const device: IOmniTrustedDevices = {
        deviceVersion: iterator.DEVICE_VERSION,
        deviceLastLogin: iterator.LAST_LOGIN_DATE_STR,
        status: iterator.DEVICE_STATUS=='B'? 'device_block_key':iterator.DEVICE_STATUS=='A'? 'device_active_key': iterator.DEVICE_STATUS,
        statusCode: iterator.DEVICE_STATUS,
        nickName: iterator.DEVICE_MODEL + ' - ' + iterator.DEVICE_PLATFORM,
        userSecId:iterator.USR_SEC_ID,        
      };
      list.push(device);
    }
    return list;
  }

}
