import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { IOptionsTemplateMultiOutlets } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { OmniBasePage } from '../omni-base/omni-base.page';
import { PaymentPage } from '../payment/payment.page';


@Component({
  selector: 'multi-outlet',
  templateUrl: './multi-outlet.page.html',
})
export class MultiOutletsPage extends OmniBasePage implements OnInit {
  private formGroup = new FormGroup({});
  public multiTranfersPageData: any = {};

  public options: IOptionsTemplateMultiOutlets = {
    outletName: 'multi-transfers',
    requestObject: this.multiTranfersPageData,
    type: 'page',
    segmentName: 'transfer-',
    dropdownList: this.generateListOfMenuItems(),
    component: PaymentPage,
    group: this.formGroup,
    operID: CommonBussinessConstant.MULTI_TRANSFER_OPER_ID,
    segmentNameUpdate: true,
    segmentNameMap: {
      key: 'toAccount',
      value: 'briefName'
    }
  };
  menuArray: any[];


  constructor(
    public commonService: PsCommonService,
    public logger: LoggerService,
  ) {
    super();
  }

  generateListOfMenuItems() {
    this.menuArray = [];
    ConstantCommon.CHECK_PAYMENT_OPERS.forEach(element => {
      const pageData = this.commonProv.getPageByOperId(element);
      if(pageData){
        this.menuArray.push({
          itemValue: pageData.OPER_ID,
          description: this.commonService.translate(pageData.OPER_NAME ? pageData.OPER_NAME : pageData.DESCRIPTION),
          selectedObj: {
            itemValue: pageData.OPER_ID,
            description: this.commonService.translate(pageData.OPER_NAME ? pageData.OPER_NAME : pageData.DESCRIPTION),
            param: pageData.PARAM,
            iconName: pageData.ICON_NAME
          }
        });
      }
    });
    return this.menuArray;
  }

  ngOnInit() {
    super.init();
  }

  ionViewDidEnter() {
    super.ionViewDidEnter();
  }

}
