import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { PsCommonBusinessSettings } from 'src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { IOptionsPsContainerPanel, IOptionsPsKeyinInput, IOptionsPsTemplateForm } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { OmniBasePage } from '../omni-base/omni-base.page';
import { PsCommonSettings } from "src/app/commonSRC/psServices/models/ps-common.settings";


@Component({
  selector: 'bank-verification-number',
  templateUrl: './bank-verification-number.page.html',
})
export class BankVerificationNumberPage extends OmniBasePage implements OnInit {

  constructor(
    public commonService: PsCommonService) {
    super();
  }

  public formGroup: FormGroup = new FormGroup({});

  userInformation = this.commonService.session.getValueOf(ConstantCommon.USERINFO);
  public options: IOptionsPsTemplateForm = {
    group: this.formGroup,
    submitOptions: {
      extraParams: {
        customerId: this.userInformation.customerInfoCO.cif,
      },
      group: this.formGroup,
      submitServiceUrl: PsCommonSettings.serviceUrl.commonSubmitAction,
      postCallFunction: {
        func() {
          return new Promise<any>((resolve, reject) => {
            resolve(this.executionClass.addBvnToSession());
          });
        },
        executionClass: this,
        params: []
      },
    },
  };

  bvnInputOptions: IOptionsPsKeyinInput = {
    fcName: 'bvn',
    group: this.formGroup,
    labelKey: 'bvn_field_key',
    placeHolder: 'enter_bvn_key'
  };
  bvnPanelOptions: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'bvn_panel_key',
    expanded: true,
    iconName: 'bvn-icon',
  };

  ngOnInit() {
    super.init();
  }
  addBvnToSession() {
    this.userInformation.omniUserVO[CommonBussinessConstant.LINKED_BVN] = this.formGroup.controls[this.bvnInputOptions.fcName].value;
    this.commonService.session.append(ConstantCommon.USERINFO, this.userInformation);
  }

}
