import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { PsAccountsListComponent } from 'src/app/commonBussinessSRC/psComponents/ps-accounts-list/ps-accounts-list.component';
import { IOptionsPsInputNumericExposed } from 'src/app/commonBussinessSRC/psComponents/ps-keyin-input/ps-input-numeric/ps-input-numeric.component.interfaces';
import { IOptionsPsInputVarcharExposed } from 'src/app/commonBussinessSRC/psComponents/ps-keyin-input/ps-input-varchar/ps-input-varchar.component.interfaces';
import { IOptionsPsLookupOwnAccountsExposed } from 'src/app/commonBussinessSRC/psComponents/ps-lookup/ps-lookup-own-accounts/ps-lookup-own-accounts.component.interfaces';
import { IOptionsPsDropdownBusinessExposed } from 'src/app/commonBussinessSRC/psComponents/ps-select-dropdown/ps-dropdown-business/ps-dropdown-business.component.interface';
import { PsCommonBusinessSettings } from 'src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { IOptionsPsContainerPanel, IOptionsPsSelectToggle, IOptionsTemplateStepper } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { OmniBasePage } from '../omni-base/omni-base.page';

@Component({
  selector: 'pos-request',
  templateUrl: './pos-request.page.html',
  styleUrls: ['./pos-request.page.scss'],
})
export class POSRequestPage extends OmniBasePage implements OnInit {
  public formGroup: FormGroup = new FormGroup({});
  public posRequestVO = {};

  stepperOptions: IOptionsTemplateStepper = {
    stepperName: 'pos_request_key',
    isHorizontalStepper: true,
    numberOfSteps: 1,
    namesofSteps: [
      'pos_request_key',
    ],
    group: this.formGroup,
    submitOptions: {
      submitServiceUrl: PsCommonBusinessSettings.serviceUrl.POSRequest,
      group: this.formGroup,
    },
    requestObject: this.posRequestVO,
  };
  mainDetailsOptions: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'main_details_key',
    expanded: true,
    iconName: 'contact'

  };
  public accountOptions: IOptionsPsLookupOwnAccountsExposed = {
    labelKey: 'account_number_key',
    placeHolder: 'account_number_key',
    component: PsAccountsListComponent,
    group: this.formGroup,
    accountAllowedCurrencies: [],
    accountAllowedTypes: [],
    glTypes: 'G',
    fcName: 'accountNo',
    accountType: 'G',
    fromTo: 'to',
    requestObject: this.posRequestVO,
  };

  numberOfPosOptions: IOptionsPsInputNumericExposed = {
    group: this.formGroup,
    fcName: 'numberOfPOS',
    placeHolder: 'number_of_pos_key',
    labelKey: 'number_of_pos_key',
    forceTriggerChange: true
  };
  emailNotificationOptions: IOptionsPsSelectToggle = {
    labelKey: 'email_notification_key',
    group: this.formGroup,
    fcName: 'emailAlert',
    psClass: 'toggleColor'
  };

  slipHeaderOptions: IOptionsPsInputVarcharExposed = {
    labelKey: 'slip_header_key',
    placeHolder: 'slip_header_key',
    group: this.formGroup,
    fcName: 'slipHeader'
  };
  public slipHeaderLabelValues: any[] = [
  ];
  businessOccupationCodeOptions: IOptionsPsDropdownBusinessExposed = {
    group: this.formGroup,
    fcName: 'businessOccupationCode',
    labelKey: 'business_occupation_code_key',
    placeHolder: 'enter_business_occupation_key'
  }
  merchantCategoryCodeOptions: IOptionsPsDropdownBusinessExposed = {
    group: this.formGroup,
    fcName: 'mccCode',
    labelKey: 'merchant_category_code_key',
    placeHolder: 'enter_merchant_category_code_key'
  }
  stateCodeOptions: IOptionsPsDropdownBusinessExposed = {
    group: this.formGroup,
    fcName: 'stateCode',
    labelKey: 'state_code_key',
    placeHolder: 'enter_state_code_key'
  }
  localGovtAreaCodeOptions: IOptionsPsDropdownBusinessExposed = {
    group: this.formGroup,
    fcName: 'lga',
    labelKey: 'locat_govt_area_code_key',
    placeHolder: 'enter_locat_govt_area_code_key'
  }
  constructor(public commonService: PsCommonService, public logger: LoggerService) {
    super();
  }


  ngOnInit() {
  }
  ionViewDidEnter() {
    super.ionViewDidEnter();
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.emailNotificationOptions.fcName], 0);

  }
  public onChangeNumberOfPos(count) {
    this.slipHeaderLabelValues = [];
    for (let i = 1; i <= count.newValue; i++) {
      this.slipHeaderLabelValues.push({
        labelKey: count.newValue == 1 ? 'slip_header_key' : 'slip_header_key' + '[' + i + '] of [' + count.newValue + ']',
        placeHolder: 'slip_header_key',
        group: this.formGroup,
        fcName: 'slipHeader' + i.toString()
      });
    }
    //    this.slipHeaderLabelValues;
  }
}
