import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { IOptionsPsTemplateApproveRejectData } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { OmniBasePage } from '../omni-base/omni-base.page';

@Component({
  selector: 'todo-approve-reject',
  templateUrl: './todo-approve-reject.page.html',
})
export class TodoApproveRejectPage extends OmniBasePage implements OnInit {
  approveRejectOptions: IOptionsPsTemplateApproveRejectData = {
    data: [],
    requestObject: {},
    group: new FormGroup({})
  }

  constructor(private logger: LoggerService, private common: PsCommonService, private navService: PsNavigatorService) {
    super();
  }

  ngOnInit() {
    let params = this.navService.getAllParams();
    if (params && params.JBPM) {
      if (params.JBPM.screenMarkup && params.JBPM.screenMarkup.queryParams) {
        this.approveRejectOptions.data = [];
        this.approveRejectOptions.isPendingDeletion = false;
        let queryParams = params.JBPM.screenMarkup.queryParams;
        let keys = Object.keys(params.JBPM.screenMarkup.queryParams);       
        keys.forEach(element => {
          let value = queryParams[element];
          this.approveRejectOptions.data.push({
            labelkey: element,
            value: value.MAP_VALUE
          })
        });
      }
      this.approveRejectOptions.extraParams = { ...params.JBPM.taskInfo }
    }
  
  if(params && params.ScreenVO){
     this.approveRejectOptions.data = [];
     let queryParams = params.ScreenVO;
     let Keys = Object.keys(params.ScreenVO);
    this.approveRejectOptions.isPendingDeletion = true;
     Keys.forEach(element => {    
         let value = queryParams[element];
          this.approveRejectOptions.data.push({
            labelkey: element,
            value: value
          })
        });
    
     
  }
  }
}
