import { Component, OnInit } from '@angular/core';
import { IPageBussiness } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-menu/ps-complex-menu.component.interfaces';
import { IOptionsPsActionGallery } from 'src/app/commonSRC/psComponents/ps-base/ps-base-action/ps-action-gallery/ps-action-gallery.interfaces';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { PsApplicationSettings } from 'src/app/commonSRC/psServices/models/ps-app-settings';
import { OmniBasePage } from '../omni-base/omni-base.page';


@Component({
  selector: 'home-page',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage extends OmniBasePage implements OnInit {

  bannersOptions: IOptionsPsActionGallery = {
    layout: 'slider'
  };
  landingType;

  videoUrl;
  constructor() {
    super();
  }
  navigated=false;
  refreshFlag = true;

  ngOnInit() {
    this.landingType = PsApplicationSettings.CLIENT_ASSETS_CONFIG.UI_CONFIGURATION.LANDING_UI_TYPE;
    super.init();
    if (this.commonProv.isWebLayout() || this.landingType == 4) {
      const oper = PsApplicationSettings.CLIENT_ASSETS_CONFIG.UI_CONFIGURATION.LANDING_PAGE_OPER;
      if (CommonUtils.isNotNull(PsApplicationSettings.CLIENT_ASSETS_CONFIG.UI_CONFIGURATION.LANDING_PAGE_OPER)) {
        this.navigated=true;
        const page: IPageBussiness = {
          operID: oper
        };
        this.navigationServices.navigateToMain();
      }
    }
    if (!this.navigated){
      this.videoUrl = PsApplicationSettings.CLIENT_ASSETS_CONFIG.IMGS_FOLDER_URL + 'welcome.mp4';
    }
  }

  ionViewWillEnter() {
    const params = this.navigationServices.getAllParams();
    if (this.commonProv.isMobileLayout() && params && params.navigationType == 'main') {
      this.refreshFlag = false;
      setTimeout(() => {
        this.refreshFlag = true;
      }, 100);
    }
  }
}
