import { Component, OnInit } from '@angular/core';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsApplicationSettings } from 'src/app/commonSRC/psServices/models/ps-app-settings';
import { OmniBasePage } from '../omni-base/omni-base.page';
import { IOptionsPsWebviewContainerExposed } from 'src/app/commonBussinessSRC/psComponents/ps-webview-container/ps-webview-container.component.interface';

@Component({
  selector: 'webview-onboarding-page',
  templateUrl: './webview-onboarding-page.page.html',
  styleUrls: ['./webview-onboarding-page.page.scss'],
})
export class WebviewOnboardingPagePage extends OmniBasePage implements OnInit {
  isLoadingUrl = true;
  webviewContainerOptions: IOptionsPsWebviewContainerExposed = {
    url: '',
    pageTitle: 'become_our_customer_key',
    showLoadingSpinner: true,
    loadingText: 'loading_key',
    showBackButton: true,
    backButtonIconName: 'arrow-back'
  };


  ngOnInit() {
    super.ngOnInit();
  }

  ngAfterViewInit() {
    this.webviewContainerOptions.url = PsApplicationSettings?.MAIN_CONFIG?.WEBVIEW_URLS?.WEBVIEW_ONBOARDING_URL;
    this.isLoadingUrl = false;
  }

  closeWebView() {
    this.navigationServices.navigateBackward();
  }

}
