import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { ViewWillEnter } from '@ionic/angular';
import { PsPayIdBeneficiaryOptionsExposed } from 'src/app/commonBussinessSRC/psComponents/ps-beneficiary/ps-payId-beneficiary/ps-payId-beneficiary.component.interfaces';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { IOmniBeneficiaryRequest } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-interfaces';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { IOptionsPsLabel, IOptionsPsTemplateView } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { OmniBasePage } from '../omni-base/omni-base.page';

@Component({
  selector: 'payId-beneficiary-list',
  templateUrl: './payId-beneficiary-list.page.html',
  styleUrls: ['./payId-beneficiary-list.page.scss'],
})
export class PayIdBeneficiaryListPage extends OmniBasePage implements OnInit, ViewWillEnter {

  private formGroup = new FormGroup({});
  public payIdBenefOptions: PsPayIdBeneficiaryOptionsExposed = {};
  public templateViewOptions: IOptionsPsTemplateView = {
    group: this.formGroup
  };

  noTransfersOptions: IOptionsPsLabel = {
    labelKey: 'no_beneficiaries_available_key'
  };

  requestSent;
  constructor(public omniPull: OmniPullService, public logger: LoggerService) {
    super();
  }

  ngOnInit() {
    super.init();
    this.payIdBenefOptions = {
      group: this.formGroup,
      isEditable: true
    }
  }

  ionViewWillEnter() {
    super.ionViewWillEnter();
    this.loadPayIdBeneficiaries();
  }

  async loadPayIdBeneficiaries() {
    const requestData: IOmniBeneficiaryRequest = {
      trxTypeParameterName: CommonBussinessConstant.PAYID,
      filterByOper: true,
      operId: CommonBussinessConstant.PAYID_BENEFICIARY_OPER_ID
    }
    let result: any = await this.omniPull.returnBeneficiaries(requestData).catch(error => {
      this.payIdBenefOptions.listOfOptions = [];
      this.logger.error('Error: While fetching international beneficiaries in BeneficiaryListPage :', error);
    });
    // demo
    if (result) {
      this.requestSent = true;
      this.payIdBenefOptions.listOfOptions = [];
      result.gridModel = [{
        submitFieldValueMap: {
          nickName: 'IBA Wallet',
          linkedAccount: '00018000132160353140272',
          payIdType: 'P',
          status: 'Active',
          payIdTypeDescriptionConcat: 'Phone Number',
          phoneNumber: '376712345',
          lookupKey: '1',
          purpose: 'business wallet',
          previewProperty: 'nickName',
          statusCode: 'A'
        },
        dataSaveId: 1
      },
      {
        submitFieldValueMap: {
          nickName: 'NAB Wallet',
          linkedAccount: '00018000132160353140272',
          payIdType: 'P',
          status: 'Active',
          payIdTypeDescriptionConcat: 'Email Address',
          emailaddress: 'payID2@azentio.com',
          lookupKey: '2',
          purpose: 'business wallet',
          previewProperty: 'nickName',
          statusCode: 'A'
        },
        dataSaveId: 2
      },
      ]
      this.payIdBenefOptions.listOfOptions = result.gridModel.length > 0 ? this.populatePayIdBeneficiaries(result.gridModel) : [];
    }
  }

  populatePayIdBeneficiaries(dataList) {
    const benefList = [];
    for (const iterator of dataList) {
      if (iterator && iterator.submitFieldValueMap) {
        const payIdBeneficiary: any = {
          nickName: iterator.submitFieldValueMap.nickName,
          payIdType: iterator.submitFieldValueMap.payIdType,
          payIdTypeDescriptionConcat: iterator.submitFieldValueMap.payIdTypeDescriptionConcat,
          payIdContent: iterator.submitFieldValueMap.payIdTypeDescriptionConcat + ' - ' + (iterator.submitFieldValueMap.phoneNumber || iterator.submitFieldValueMap.emailAddress || iterator.submitFieldValueMap.mobileNumber),
          phoneNumber: iterator.submitFieldValueMap.phoneNumber,
          emailAddress: iterator.submitFieldValueMap.emailAddress,
          mobileNumber: iterator.submitFieldValueMap.mobileNumber,
          purpose: iterator.submitFieldValueMap.purpose,
          dataSaveId: iterator.dataSaveId,
          previewProperty: 'nickName',
          status: iterator.status === 'A' ? 'active_key' : 'pending_for_approval_key'
        }
        benefList.push(payIdBeneficiary);
      }
    }
    return benefList;
  }

}
