import { AfterViewInit, Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IOptionsPsActionImageExposed, IOptionsPsContainerPanel, IOptionsPsKeyinInputExposed, IOptionsPsTemplateView } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { OmniBasePage } from '../omni-base/omni-base.page';


@Component({
  selector: 'about-app',
  templateUrl: './about-app.page.html',
  styleUrls: ['./about-app.page.scss'],
})
export class AboutAppPage extends OmniBasePage implements OnInit, AfterViewInit {

  formGroup: FormGroup = new FormGroup({});

  public templateViewOptions: IOptionsPsTemplateView = {
    group: this.formGroup
  };

  aboutApppanelOption: IOptionsPsContainerPanel = {
    isExpandable: false,
    labelKey: 'about_app_info_key',
    iconName: 'info',
    expanded: true
  };

  appNameOptions: IOptionsPsKeyinInputExposed = {
    placeHolder: 'app_name_key',
    labelKey: 'app_name_key',
    group: this.formGroup,
    fcName: 'appName'
  };
  appVersionOptions: IOptionsPsKeyinInputExposed = {
    placeHolder: 'app_version_key',
    labelKey: 'app_version_key',
    group: this.formGroup,
    fcName: 'appVersion'
  };
  copyrightYearOptions: IOptionsPsKeyinInputExposed = {
    placeHolder: 'copyright_year_key',
    labelKey: 'copyright_year_key',
    group: this.formGroup,
    fcName: 'copyrightYear'
  };

  appImageOptions: IOptionsPsActionImageExposed;

  constructor(public loggerP: LoggerService, private omniPull: OmniPullService) {
    super();
  }

  ngOnInit() {
    super.init();
    this.appImageOptions = {
      imageName: unescape(CommonUtils.getCssVariableValue('--app-logo')),
      allowCust: false,
      psClass: 'profile-info'
    }
  }

  ngAfterViewInit() {
    const APP_COPYRIGHT_YEAR = "CopyrightYear";
    this.omniPull.getParamValOf(APP_COPYRIGHT_YEAR).then(result => {
      if (result && result[APP_COPYRIGHT_YEAR]) {
        this.formGroup.controls[this.copyrightYearOptions.fcName].setValue(result[APP_COPYRIGHT_YEAR]);
      }
    }).finally(() => {
      this.formGroup.controls[this.appVersionOptions.fcName].setValue(PsCommonSettings.appVersionNumber);
    })
    this.commonProv.returnAppName().then(result => {
      if (result) {
        this.formGroup.controls[this.appNameOptions.fcName].setValue(result);
      }
    });
  }
}
