import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { NavController } from '@ionic/angular';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { IOptionsTemplateLogin } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-interfaces';
import { IOptionsPsActionButton, IPageCommon } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsTemplateBasePage } from 'src/app/commonSRC/psTemplates/ps-template-base/ps-template-base.page';
import { PsApplicationSettings } from 'src/app/commonSRC/psServices/models/ps-app-settings';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';

@Component({
  selector: 'app-welcome',
  templateUrl: './welcome.page.html',
  styleUrls: ['./welcome.page.scss'],
})
export class WelcomePage extends PsTemplateBasePage implements OnInit {
  public mainOptions: IOptionsTemplateLogin = {};
  private formGroup = new FormGroup({});
  loginOptions: IOptionsPsActionButton = {
    //labelKey: 'login_key',
    labelKey: 'Log in',
    group: this.formGroup,
    type: 'button',
  };
  getstartedOptions: IOptionsPsActionButton = {
    //labelKey: 'get_started_key',
    labelKey: 'Get Started',
    group: this.formGroup,
    type: 'button',

  };

  constructor(private navCtrl: NavController) {
    super();
  }

  ngOnInit() {
    super.init();
  }

  onButtonClickedGetStarted(event) {
    const page: IPageCommon = {
      operID: CommonBussinessConstant.ON_BOARDING_OPER,
      title: 'become_our_customer_key',
      icon: 'person-add',
      component: 'iba-onboarding',
    };
    this.navService.openPage(page);
  }
  onButtonLoginClicked(event) {
    this.navCtrl.navigateForward('omni-login');

  }
  
  ionViewDidEnter() {
    // Added by Richie to log the time of of the specific action
    if (PsApplicationSettings.MAIN_CONFIG.TIME_LOG_ENABLED) {
      PsCommonSettings.timeLogs.push('in welcome page, ionViewDidEnter with start time: ' + (new Date()).toISOString());
    }
    // end Richie
    
    PsCommonSettings.VGn7PhYqt0YnWCou = false;
    
    this.commonProv.sendTimeLog(); // send time logs to crashlytics

  }

}
