import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { ISubscriberInfoResponse } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-interfaces';
import { PsCommonBusinessSettings } from 'src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { IOptionsPsLabel, IOptionsPsTemplateForm } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { OmniBasePage } from '../omni-base/omni-base.page';

@Component({
  selector: 'ussd-deactivation',
  templateUrl: './ussd-deactivation.page.html',
  styleUrls: ['./ussd-deactivation.page.scss'],
})
export class UssdDeactivationPage extends OmniBasePage implements OnInit {
  public formGroup: FormGroup = new FormGroup({});
  public securityVO: any = {};
  public options: IOptionsPsTemplateForm = {
    group: this.formGroup,
    submitOptions: {
      extraParams: {},
      group: this.formGroup,
      submitServiceUrl: PsCommonBusinessSettings.serviceUrl.ussdDeactivationRequestEndPoint,
    }
  };
  ussddeactivationLabelOptions: IOptionsPsLabel = {
    labelKey: 'ussd_deactivation_msg_key'
  };
  constructor(public commonProv: PsCommonService, public loggerP: LoggerService, private omniPull: OmniPullService) {
    super();
  }


  ngOnInit() {
    super.init();
    const subscriberInfo: ISubscriberInfoResponse = this.commonProv.session.getValueOf(ConstantCommon.SUBSCRIBER_INFO);
    if (subscriberInfo && subscriberInfo.subscriberInfo) {
      this.commonInfoHandling(subscriberInfo.subscriberInfo);
    } else {
      this.commonProv.presentLoading();
      this.getSubscriberInfo();
    }
  }

  async getSubscriberInfo() {
    this.omniPull.returnSubscriberDetails().then(userInformation => {
      this.commonInfoHandling(userInformation.subscriberInfo);
      this.commonProv.dismissLoading();
    }).catch(error => this.commonProv.logger.log(error))
  }

  private commonInfoHandling(subscriberInfo) {
    this.commonProv.copyObject(this.options.submitOptions.extraParams, {
      phoneNo: subscriberInfo.phonesList ? subscriberInfo.phonesList[0] : ''
    }, false, true);
  }
}
