import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { IOptionsTemplateBPMFlow } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { OmniBasePage } from '../omni-base/omni-base.page';

@Component({
  selector: 'app-bpm-dynamic-page',
  templateUrl: './bpm-dynamic-page.page.html'
})
export class BpmDynamicPagePage extends OmniBasePage implements OnInit {

  constructor(private navService: PsNavigatorService) {
    super();
  }
  id = "";
  bpmOptions: IOptionsTemplateBPMFlow = {
    group: new FormGroup({})
  }

  navParam = {};

  ngOnInit() {
    super.init();
    this.id = PsCommonSettings.oper_ID.toString();
    this.navParam = this.navService.getAllParams();
    this.bpmOptions.bpmTemplate = this.navParam;
  }

}
