import { Component, Input, OnInit } from '@angular/core';
import { IOptionsPsComplexReportFilterCriteriaComponent } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-report-filter-criteria/ps-complex-report-filter-criteria.component.interfaces';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';

import { CommonUtils } from '../../psServices/models/common-utils';
import { PsCommonService } from '../../psServices/ps-common/ps-common.service';
import { CommonCustUtils } from './../../customization/psServices/util/common-cust-utils';
import { IOptionsPsButtonCancel, IOptionsPsButtonPrint, IOptionsPsButtonSendEmail, IOptionsPsContainerReportViewer, IOptionsPsInputSearchHTMLExposed, IOptionsPsTemplateReport, IOptionsTemplateScreen } from './../../psServices/models/ps-common-interface';
import { PsTemplateBasePage } from './../ps-template-base/ps-template-base.page';
import { IOptionsPsButtonExport } from 'src/app/commonBussinessSRC/psComponents/ps-button-export/ps-button-export.component.interface';

@Component({
  selector: 'ps-template-report',
  templateUrl: './ps-template-report.template.html',
  styleUrls: ['./ps-template-report.template.scss'],
})
export class PsTemplateReport extends PsTemplateBasePage implements OnInit {

  @Input() options: IOptionsPsTemplateReport;
  expanded = false;
  customizationMode = false;
  screenOptions: IOptionsTemplateScreen ={};

  reportViewerOptions: IOptionsPsContainerReportViewer = {
    group: this.options.group,
  };
  reportFilterOptions: IOptionsPsComplexReportFilterCriteriaComponent = {};
  searchOptions: IOptionsPsInputSearchHTMLExposed = {
    contentReference: 'div-content',
    selectors:"td span"
  };

  cancelOptions: IOptionsPsButtonCancel = {
    group: this.options.group
  };
  printOptions: IOptionsPsButtonPrint = {
    group: this.options.group
  };
  exportOptions: IOptionsPsButtonExport = {
    group: this.options.group
  };
  sendOptions: IOptionsPsButtonSendEmail = {
    group: this.options.group
  }
  shareOptions: IOptionsPsButtonSendEmail = {
    group: this.options.group
  }
  displayDiv = false; //Mohanad: AZDB-5549-01092024 - Set flag to false by default,so only init() can set it to true to invoke report-container ngOnInit
  filteredData = {};

  isMobile = true;

  constructor(protected common: PsCommonService) {
    super();
  }

  isLoggedIn = PsCommonSettings.isLoggedIn;

  listOfFields: Array<any> = [this.reportViewerOptions, this.printOptions, this.exportOptions, this.shareOptions, this.sendOptions];


  ngOnInit() {
    this.isMobile = CommonUtils.isNativeMobile();
    this.customizationMode = CommonCustUtils.returnCustMode();
    this.cancelOptions.nextPageOptions = this.options.nextPageOptions; //added by Marina TP:#1586190;
    this.screenOptions.nextPageOptions = this.options.nextPageOptions; //added by Marina TP:#1586190;
  }

  init() {
    if(this.options.reportParametersList && this.options.reportParametersList.navigationType=="backward"){
      this.options.reportParametersList=this.common.activePage.value.param;
    }
    this.listOfFields.forEach((field) => {
      field.operId = this.options ? this.options.operId : 1;
      field.reportParametersList = { ...this.options.reportParametersList, ...this.filteredData };
    });
    this.displayDiv = true;
    this.reportViewerOptions.showReport = this.options.showReport;
    if (this.options.timeOut) {
      this.reportViewerOptions.timeOut = {};
      this.reportViewerOptions.timeOut.timeOutCount = this.options.timeOut.timeOutCount;
      this.reportViewerOptions.timeOut.timeOutFunc = this.options.timeOut.timeOutFunc;
      this.reportViewerOptions.timeOut.message = this.options.timeOut.message;
    }
    this.cancelOptions.nextPageOptions = this.options.nextPageOptions; //added by Marina TP:#1586190;
    this.screenOptions.nextPageOptions = this.options.nextPageOptions; //added by Marina TP:#1586190;
  }

  filterCriteria(filterData) {
    this.sendOptions.fromDate = filterData.filterFromDate
    this.sendOptions.todate = filterData.filterToDate
    this.filteredData = {...filterData};
    this.refreshReport();
  }

  refreshReport() {
    this.displayDiv = false;
    this.options.showReport = true;
    setTimeout(() => {
      this.init();
    }, 500);
  }
  collapseExpandFilter(data) {
    this.expanded = data;
  }
}
